.rigth {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet {
        align-items: flex-end;
    }

    .dark-mode-button {
        width: 3rem;
        margin-bottom: 2rem;
        margin-top: 3rem;
        filter: invert(100%);

        &:hover {
            cursor: pointer;
        }
        
        @include tablet {
            margin-top: 0;
        }
    }
}


.bar {
    display: flex;
    flex-direction: column;
    padding-top: 3rem;
    align-items: center;

    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }
}

.navigation {

    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear;
    
    @include tablet {
        display: flex;
        flex-direction: row;
        opacity:1;
        visibility: visible;
    }
    a {
        color: $white;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;

        @include tablet {
            margin-right: 2rem;
            font-size: 1.8rem;

            &:last-of-type {
                margin-right: 0;
            }
        }
    }
}


/* Menú desplegable */

.mobile-menu {
    @include tablet {
        display: none;
    }

    img {
        width: 5rem;
    }
}

.show {
    visibility: visible;
    opacity: 1;
    height: auto;
}
