@include tablet {
    .advertisements-container {
        @include grid(3, 2rem)
    }
}

.advertisement {
    background-color: lighten($grey, 5);
    border: 1px solid darken($grey, 10);
    margin-bottom: 2rem;

    .advertisement-content {
        padding: 2rem;
    }

    .h3, p {
        margin: 0;
    }

}
.price {
    color: $green;
    font-weight: $bold;
    font-size: 3rem;
}
.icons-features {
    display: flex;
    list-style: none;
    padding: 0;
    max-width:40rem;

    li {
        flex: 1;
        display: flex;

        img {
            flex: 0 0 3rem;
            margin-right: 1rem;
        }
    }
}
